/* Global styles */
body {
  font-family: Arial, sans-serif;
  margin: 0;
  background: #f7f9fb;
  color: #222;
}
header, footer {
  background: #145da0;
  color: #fff;
  padding: 1rem;
  text-align: center;
}
nav {
  background: #d4e6f1;
  padding: 0.5rem 1rem;
}
.sidebar {
  background: #d4e6f1;
  padding: 1rem;
  min-width: 180px;
}
.main-content {
  padding: 2rem;
}

/* Header styles */
.site-header {
  width: 100%;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 10;
  background: transparent;
  transition: background 0.3s, box-shadow 0.3s;
}
.site-header.scrolled {
  background: #fff;
  box-shadow: 0 2px 8px rgba(34, 44, 54, 0.08);
}
.header-container {
  max-width: 1200px;
  margin: 0 auto;
  display: flex;
  align-items: center;
  position: relative;
  height: 80px;
  padding: 0 32px;
}
.header-logo {
  width: 60px;
  height: 60px;
  position: absolute;
  left: 32px;
  top: 10px;
}
.header-title {
  position: absolute;
  left: 120px;
  top: 0;
  height: 80px;
  display: flex;
  align-items: center;
  color: black;
  font-size: 36px;
  font-family: Roboto, Arial, sans-serif;
  font-weight: 400;
  word-wrap: break-word;
}
.header-nav {
  position: absolute;
  left: 420px;
  top: 28px;
  display: flex;
  gap: 50px;
  align-items: center;
}
.header-link {
  color: #2B2C34;
  font-size: 20px;
  font-family: 'Plus Jakarta Sans', Arial, sans-serif;
  font-weight: 700;
  text-transform: capitalize;
  text-decoration: none;
  word-wrap: break-word;
}
.header-search {
  display: flex;
  align-items: center;
  gap: 10px;
}
.search-icon {
  width: 17px;
  height: 17px;
  display: inline-block;
  background: #2B2C34;
  border-radius: 50%;
}

/* Responsive Styles */
@media (max-width: 900px) {
  .header-nav { left: 250px; }
  .header-title { left: 100px; font-size: 28px; }
  .header-logo { left: 16px; }
}
@media (max-width: 600px) {
  .header-container { padding: 0 10px; }
  .header-nav { left: 110px; gap: 25px; }
  .header-title { left: 80px; font-size: 20px; }
  .header-logo { left: 4px; width: 40px; height: 40px; top: 20px;}
}
@media (max-width: 768px) {
  .main-content, .sidebar {
    padding: 1rem;
  }
}

/* End of combined styles */